body {
	background-color: #F0F0F0
}

.home-container {
	display: grid;
	grid-template-rows: 470px 150px;
	margin-top: -2%;
}

.home-body {
	display: grid;
	font-family: "Times New Roman", Times, serif;
	font-style: italic;
	line-height: normal;
	font-size: 5.2em;
	justify-content: center;
	align-items: center;
}

.home-apply {
	display: grid;	
	justify-content: center;
	align-items: center;
	margin-left: auto;
  	margin-right: auto;
}

.home-vector1 {
	width: 180px;
	opacity: 0.95;
	margin-top: 95px;
	margin-left: 10px;
}

.home-apply-link {
	position: fixed;
	color: white;
	text-decoration: none;
	font-family: "Arial", sans-serif;
	font-weight: bolder;
	font-size: 1.2em;
	margin-top: 135px;
	margin-left: 50px;
}
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background-color: #F0F0F0
}

form {
	display: grid;
	grid-template-columns: 325px 325px;
	grid-column-gap: 100px;	
	grid-row-gap: 20px;
	justify-content: center;
	align-items: center;
	margin-top: 6%
}

.submit {
	background-color: #FC08D5; /* Pink */
	border: none;
	color: white;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	height: 32px;
	width: 80px;
}

.disclaimer {
	display: grid;
	font-family: "Times New Roman", Times, serif;
	font-style: italic;
	font-size: .6em;
	grid-column: 1 / 3;
}
